@import '~Styles/colors.module';

.flex_2 {
  flex: 2;
}

.flex_1 {
  flex: 1;
}

.coupon_header_text {
  font-size: 12px;
  font-weight: bold;
  color: white;
  margin: 24px 0px 10px;
}

.checkout_container {
  padding: 20px 60px;
  overflow: scroll;

  .cart_list_container {
    flex: 1;
    padding-right: 50px;

    .table_header {
      padding: 8px;
      font-size: 12px;
      font-weight: bold;
      line-height: 2;
      color: $primary-text-color;
      border-bottom: 1px solid #ededed;
    }

    .table_content_container {
      overflow: scroll;
    }
  }


  .order_summary_container {
    width: 360px;
    align-self: stretch;
    padding: 15px 30px 15px;
    // overflow: scroll;

    border-radius: 10px;
    box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.08);
    background-image: linear-gradient(210deg, #37445b 116%, $primary-text-color 20%);

    .order_summary_title {
      font-family: PlayfairDisplay;
      font-size: 36px;
      font-weight: 900;
      line-height: 1.56;
      color: white;
      align-self: center;
    }

    .coupon_input {
      align-self: stretch;
      border-radius: 30px;
      border: solid 1px rgba(white, 0.6);
      padding: 12px 26px;
      margin: 19px 0px;
    
      .icon {
        width: 20px;
      }

      .input {
        background: none;
        border: none;
        margin: 0px 6px;
        color: white;
        font-size: 12px;
        font-weight: bold;

        ::placeholder {
          color: rgba(white, 0.8);
        }
      }

      .apply_button {
        margin-left: auto;
        font-size: 12px;
        color: $color-orangey-red;
      }
    }

    .coupon_description_container {
      color: white;
      border: solid 1px white;
      width: 80%;
      margin-bottom: 24px;

      .coupon_content_container {
        padding: 17px 9px 9px;

        .coupon_title {
          font-size: 10px;
          font-weight: bold;
        }
  
        .coupon_description {
          font-size: 8px;
          font-weight: 300;
        }  
      }

      .coupon_container {
        padding: 9px 14px;

          .coupon {
            font-size: 10px;
            font-weight: bold;
            color: $primary-text-color;
            border: solid 1px $color-orangey-red;
            background-color: $color-very-light-blue;
          }

          .coupon_apply {
            font-size: 10px;
            color: rgba($color-orangey-red, 0.8);
            margin-left: auto;
          }
      }
     }
    }

    .price_details_container {
      padding: 6px 0px;

      .title {
        font-size: 12px;
        font-weight: 300;
        color: white;
        opacity: 0.8;
      }

      .value {
        font-size: 12px;
        font-weight: 300;
        color: white;
        opacity: 0.8;
        margin-left: auto;
      }
  }

  .price_divider {
    margin-top: 12px;
  }
  
  .capsule_button {
    padding: 16px 0px;
    align-self: stretch;
    font-size: 18px;
    font-weight: 900;
    color: white;
    text-transform: capitalize;
    margin-top: 19px;
  }
}
