@import '~Styles/colors.module';

.help_center_container {
  overflow: auto;

  .list_container {
    padding: 35px 36px;

    .item_container {
      width: 207px;
      height: 207px;
      border-radius: 2px;
      border: solid 1px rgba($color-light-blue-grey, 0.5);
  
      .item_icon {
        height: 42px;
        opacity: 0.7;
      }
  
      .item_title {
        text-align: center;
        font-size: 14px;
        font-weight: bold;
        color: $primary-text-color;
        margin: 18px 0px 9px;
      }
  
      .item_description {
        text-align: center;
        font-size: 13px;
        font-weight: 300;
        color: $primary-text-color;
      }
    }
  }


  .form_header {
    font-size: 12px;
    color: $primary-text-color;
    padding: 12px 46px;
    border-top: 0.5px solid #ededed;
    border-bottom: 0.5px solid #ededed;
  }

  .form_container {
    padding: 22px 35px;

    .text_area {
      width: 393px;
      height: 180px;
      resize: none;
      border: none;
      background: rgba($color-very-light-blue, 0.4);
      padding: 14px 31px;
    }
  }

}
